        evidences: ["Medidor EMF 5", "Ultravioleta", "Orbes espectrales"],
        info: "Cada vez que un <strong>Obake</strong> interacta con una superficie vlida, tiene un <strong>75% de probabilidad de dejar huellas dactilares</strong> (en vez del 100% normal). Al pisar sal, tambin tiene un <strong>75% de probabilidad de dejar pisadas</strong> y, si falla, <strong>no genera sonido de pasos</strong>. En <strong>multijugador</strong>, las huellas y sonidos pueden no sincronizarse correctamente entre jugadores, mostrando un nmero diferente de huellas y pasos.<br><br>Al crear huellas, hay un <strong>16.7% de probabilidad</strong> de que sean <strong>huellas nicas</strong>: <strong>seis dedos</strong>, <strong>dos huellas en interruptores/lmparas/controles</strong>, <strong>cinco en teclados o puertas de celda</strong>.<br><br>El Obake puede activar una habilidad especial que <strong>reduce a la mitad el tiempo restante de todas las huellas</strong> en el mapa, pudiendo hacer que <strong>desaparezcan en segundos</strong>. Esta habilidad puede activarse varias veces seguidas.<br><br>Durante eventos fantasmales de <strong>caceras falsas/persecucin</strong> o el evento de la <strong>Caja Musical</strong>, es el nico fantasma que a veces <strong>no genera sonido de pasos tras pisar sal</strong>.<br><br>Durante una <strong>cacera</strong>, el Obake puede <strong>cambiar de modelo</strong> en ciertos parpadeos (solo visible para jugadores vivos). Los cambios ocurren en parpadeos especficos y pueden ser muy notorios si usa modelos de nio o paciente.<br><br>Si el fantasma <strong>toca una superficie y no deja huellas, pero luego s en otra, o viceversa</strong>, suele indicar que es un Obake.<br><br>Para identificarlo, revisa cada interaccin con <strong>luz UV</strong> y observa si las huellas <strong>desaparecen rpido</strong>, si <strong>faltan sonidos de pasos</strong> o si <strong>cambia de modelo durante una cacera</strong>. En partidas sin evidencia, solo se puede identificar por su <strong>cambio de modelo</strong> o <strong>falta de sonidos de pasos</strong>.<br><br>En dificultad personalizada, la <strong>probabilidad y el tiempo de desaparicin de huellas</strong> pueden variar.<br><br>En partidas sin evidencia, <strong>nunca dejar huellas ni pisadas</strong>; se identifica por su habilidad de <strong>cambiar de modelo durante la cacera</strong>. En multijugador, un jugador puede esconderse y otros observar por cmaras para ver el cambio de modelo. En un jugador, es til buscar lugares donde se pueda ver bien al fantasma mientras haces un loop.<br><br>El uso de <strong>incienso</strong> o <strong>sal de Tier III</strong> puede facilitar la identificacin. Una <strong>cmara de video</strong> puede mostrar el mensaje <strong>\"Cambiaformas\"</strong> si apuntas al fantasma justo en el momento que cambia de modelo.<br><br>Otra forma de identificarlo es notar la <strong>falta de sonidos de pasos tras pisar sal</strong> o en eventos de cacera falsas/persecucin/Caja Musical. Si en multijugador los jugadores escuchan diferente nmero de pasos, <strong>probablemente sea un Obake</strong>.",
        tldr: " Un <strong>Obake</strong> tiene <strong>75% de probabilidad de dejar huellas</strong> y puede crear <strong>huellas nicas</strong>.<br> Sus huellas pueden <strong>desaparecer muy rpido</strong>.<br> En una cacera, <strong>puede cambiar de modelo/apariencia</strong> durante parpadeos.<br> Puede faltar <strong>sonidos de pasos</strong> tras pisar sal o en eventos.<br> En multijugador, las huellas y sonidos pueden no coincidir entre jugadores."
